import { api } from './http'

export const getAssets = async (owner, tokenAddress) => {
  return await api.get(`/v2/${owner}/nft/${tokenAddress}?chain=polygon&format=decimal&limit=100`)
}

export const getAssetById = async (tokenAddress, tokenId) => {
  return await api.get(`/v2/nft/${tokenAddress}/${tokenId}?chain=polygon&format=decimal`)
}