import React, { PureComponent } from 'react';

// import { actionCreators } from './store';
import { connect } from 'react-redux';
import { HomeWrapper } from './style.js'
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';

import CardContent from '@mui/material/CardContent';
import CardActions from '@mui/material/CardActions';

import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';

import FavoriteIcon from '@mui/icons-material/Favorite';
import ShareIcon from '@mui/icons-material/Share';
import Snackbar from '@mui/material/Snackbar';

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import TextField from '@mui/material/TextField';
// import { OpenSeaSDK, Network } from 'opensea-js'
import Web3 from 'web3'
import LazyLoad from 'react-lazyload';
// import placeholder from "../../assets/images/placeholder.png"
import { DOC_LIST, DOC_TOKEN_ADDRESS, DOC_TOKEN_ABI } from '../../config/setting'
const vertical = 'top'
const horizontal = 'right'

class Profile extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {asset: '', open: false, price: "", order: "", isOwner: false, SnackbarOpen: false, message: ""}
    } 
    
    async componentDidMount() {
        const { match } = this.props
         if(DOC_LIST[Number(match.params.tokenId) - 1]){
            this.setState({
                asset: DOC_LIST[Number(match.params.tokenId) - 1]
            })
         }else{
            this.setState({
                asset: ''
            })
       
         }
    }

    handleCliockShowSellBox = () => {
         this.setState({
            open: true
          });
    }
    handleCliockBuyNow = async () => {
         const { account, match, walletProvider } = this.props
         if(account){
            var webObj = new Web3(walletProvider)
            var myContract = new webObj.eth.Contract(DOC_TOKEN_ABI, DOC_TOKEN_ADDRESS)
            await myContract.methods.mint(account, match.params.tokenId, 1, []).send({from:account}).then((res)=>{
                console.log("res", res)
            }).catch(err=>{
                console.log("error", err)
            })
 
         }else{
            this.setState({
                SnackbarOpen: true,
                message: "Please click the wallet in the upper right corner to login"
            });
         }
    }
    handleCliockSell = async () => {
        const { price } = this.state
        const { account, opensea } = this.props
        const { tokenAddress, tokenId } = this.props.match.params
        if(account){
            if(price){
                // SELL Fixed price
                const expirationTime = Math.round(Date.now() / 1000 + 60 * 60 * 24)
                opensea.createSellOrder({
                asset: {
                    tokenAddress: tokenAddress, 
                    tokenId: tokenId, 
                },
                accountAddress: account,
                startAmount: price,
                expirationTime
                }).then(() => {
                    this.setState({
                        open: false,
                        SnackbarOpen: true,
                        message: "success"
                      })
                }).catch( err =>{
                    this.setState({
                        open: false,
                        SnackbarOpen: true,
                        message: err.message
                    });
                })
           
            }else{
                this.setState({
                    SnackbarOpen: true,
                    message: "Please enter a fixed price"
                });
            }
        }else{
            this.setState({
                SnackbarOpen: true,
                message: "Please click the wallet in the upper right corner to login"
            });
        }
    }

    handleClose = () => {
        this.setState({
          open: false
        });
    }

    handleCloseSnackbar = () => {
        this.setState({
            SnackbarOpen: false
        });
    }

    handleCnhange = e => {
        const data = e.target.value
        this.setState({
            price: data
        });
    }
    render() {
        const { asset, open, price, SnackbarOpen, message } = this.state
        
        return (
            <HomeWrapper>
               <Box sx={{ flexGrow: 1 }} className="cl-grid">
                {asset ?
                <Grid
                  container
                  spacing={2}
                  direction="row"
                  justifyContent="center"
                  alignItems="flex-start"                
                  >
                    <Grid item xs={3}>
                        <Card sx={{ maxWidth: 345 }}>
                            <LazyLoad className='cl-images'>
                                 <img src={asset.docImage} alt={asset.name}  />
                            </LazyLoad>
                            <CardContent>
                                <Typography variant="body2" color="text.secondary">
                                {asset.description}
                                </Typography>
                            </CardContent>
                            <CardActions disableSpacing>
                                <IconButton aria-label="add to favorites">
                                <FavoriteIcon />
                                </IconButton>
                                <IconButton aria-label="share">
                                <ShareIcon />
                                </IconButton>
                            </CardActions>
                        </Card>
                    </Grid>
                    <Grid item xs={3}>
                    <Typography variant="body2" color="text.secondary">
                        {/* {asset.collection.name} */}
                    </Typography>
                    <Typography gutterBottom variant="h6" component="div" className='cl-nftname'>
                                  {asset.name}
                    </Typography>
                    <Button variant="contained"   onClick={()=>this.handleCliockBuyNow()}>Buy now</Button>
                    {/* {(() => {
                        if (order) {
                            return (
                                <Button variant="contained"  disabled={isOwner} onClick={()=>this.handleCliockBuyNow()}>Buy now</Button>
                            )
                        } else {
                            if(isOwner) {
                                return (
                                    <Button variant="contained"  onClick={()=>this.handleCliockShowSellBox()}>SELL</Button>
                                )
                            }
                        }
                    })()} */}
             
                    </Grid>
                 
                </Grid>
                   : "" }
                </Box>
                <Dialog
          
                    open={open}
                    onClose={this.handleClose}
                >
                    <DialogTitle>Price setting</DialogTitle>
                    <DialogContent>
                    <DialogContentText>
                       Please enter a fixed price.
                    </DialogContentText>
                    <TextField
                        autoFocus
                        margin="dense"
                        id="price"
                        label="fixed price"
                        type="name"
                        fullWidth
                        variant="standard"
                        value={price} 
                        onChange={this.handleCnhange}
                    />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handleClose} >Close</Button>
                        <Button onClick={this.handleCliockSell}>confirm</Button>
                    </DialogActions>
                </Dialog>
                <Snackbar
                    anchorOrigin={{ vertical, horizontal }}
                    autoHideDuration={6000} 
                    open={SnackbarOpen}
                    onClose={this.handleCloseSnackbar}
                    message={message}
                    key={vertical + horizontal}
                />
            </HomeWrapper>
        )
    }

}

const mapState = (state) => ({
    walletProvider: state.getIn(['header', 'walletProvider']),
    chainId: state.getIn(['header', 'chainId']),
    account: state.getIn(['header', 'account']),
    myNftAssets: state.getIn(['header', 'myNftAssets']),
})


export default connect(mapState)(Profile);