import React, { PureComponent } from 'react';

// import { actionCreators } from './store';
import { connect } from 'react-redux';
import { HomeWrapper } from './style'
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';
import Snackbar from '@mui/material/Snackbar';
// import { Button, CardActionArea, CardActions } from '@mui/material';
// import { Link } from 'react-router-dom'
import LazyLoad from 'react-lazyload';
import { DOC_LIST } from '../../config/setting';
const vertical = 'top'
const horizontal = 'right'

class Profile extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {anchorElNav: '', SnackbarOpen: false, message: ''}
    } 

    handleOpenNavMenu =  (event) => {
        this.setState({
          anchorElNav: event.currentTarget
        })
    }

    handleOpenDoc =  (e) => {
        if(this.props.account){
            this.props.history.push('/renderer/'+ encodeURIComponent(e))
            console.log(this.props.account)
        }else{
            this.setState({
                SnackbarOpen: true,
                message: "Please click the wallet in the upper right corner to login"
            });
        }
    }

    handleCloseSnackbar = () => {
        this.setState({
            SnackbarOpen: false
        });
    }
    render() {
        // const { myNftAssets } = this.props
        const { SnackbarOpen, message } = this.state;
        return (
            <HomeWrapper>
                <Grid
                container
                direction="row"
                justifyContent="flex-start"
                alignItems="flex-start"
                className='cl-grid'
                >
                { DOC_LIST? DOC_LIST.map((item, key) => (  
                   
                    <Grid item xs={2} sm={2} md={3} key={key} className="cl-card" onClick={()=>this.handleOpenDoc(item.tokenId)}>
                        
                          <Card sx={{ maxWidth: 345 }}>
                            <CardActionArea>
                                <LazyLoad className='cl-images'>
                                    <img src={item.docImage} alt={item.name}  />
                                </LazyLoad>
                                <CardContent>
                                <Typography gutterBottom variant="h6" component="div" className='cl-nftname'>
                                  {item.name}
                                </Typography>
                                <Typography variant="body2" color="text.secondary" className='cl-description'>
                                   {item.description}
                                </Typography>
                                </CardContent>
                            </CardActionArea>
                            {/* <CardActions>
                                <Link to={'renderer/'+ item.tokenAddress+'/'+item.tokenId} >
                                    <Button size="small" color="primary">
                                       Reading
                                    </Button>
                                </Link>
                            </CardActions> */}
                        </Card>
                     
                    </Grid>
                    
                 ) ) : ""}
            
                </Grid>
                <Snackbar
                    anchorOrigin={{ vertical, horizontal }}
                    autoHideDuration={6000} 
                    open={SnackbarOpen}
                    onClose={this.handleCloseSnackbar}
                    message={message}
                    key={vertical + horizontal}
                />
            </HomeWrapper>
        )
    }

}

const mapState = (state) => ({
    opensea: state.getIn(['header', 'opensea']),
    chainId: state.getIn(['header', 'chainId']),
    account: state.getIn(['header', 'account']),
    myNftAssets: state.getIn(['header', 'myNftAssets']),
})


export default connect(mapState)(Profile);