import React, { PureComponent } from 'react';

// import { actionCreators } from './store';
import { connect } from 'react-redux';
import { HomeWrapper } from './style'
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import { Button, CardActionArea, CardActions } from '@mui/material';
import { Link } from 'react-router-dom'
import LazyLoad from 'react-lazyload';

class Profile extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {anchorElNav: ''}
    } 

    handleOpenNavMenu =  (event) => {
        this.setState({
          anchorElNav: event.currentTarget
        })
    }

    render() {
        const { myNftAssets } = this.props
        console.log("myNftAssets", myNftAssets)
        return (
            <HomeWrapper>
                <Grid
                container
                direction="row"
                justifyContent="flex-start"
                alignItems="flex-start"
                className='cl-grid'
                >
                { myNftAssets? myNftAssets.map((item, key) => (  
                   
                    <Grid item xs={2} sm={2} md={3} key={key} className="cl-card">
                        
                          <Card sx={{ maxWidth: 345 }}>
                          {/* <Link to={'detail/'+item.openseaLink.split("/")[4]+'/'+ item.tokenAddress+'/'+item.tokenId} > */}
                            <CardActionArea>
                                <LazyLoad className='cl-images'>
                                    <img src={item.imageUrl} alt={item.name}  />
                                </LazyLoad>
                                <CardContent>
                                <Typography gutterBottom variant="h6" component="div" className='cl-nftname'>
                                  {item.name}
                                </Typography>
                                <Typography variant="body2" color="text.secondary">
                                
                                </Typography>
                                </CardContent>
                            </CardActionArea>
                            {/* </Link> */}
                            <CardActions>
                                <Link to={'renderer/'+item.token_id} >
                                    <Button size="small" color="primary">
                                       Reading Doc
                                    </Button>
                                </Link>
                            </CardActions>
                        </Card>
                     
                    </Grid>
                    
                 ) ) : ""}
            
                </Grid>
                
            </HomeWrapper>
        )
    }

}

const mapState = (state) => ({
    opensea: state.getIn(['header', 'opensea']),
    chainId: state.getIn(['header', 'chainId']),
    account: state.getIn(['header', 'account']),
    myNftAssets: state.getIn(['header', 'myNftAssets']),
})


export default connect(mapState)(Profile);